import React, {
	createContext, useContext, useState, Dispatch, useEffect
} from 'react';
import axios from 'axios';
import { useHistory } from 'react-router-dom';

import { AnswerObj, childrenObj, QuestionAnswerObj } from '../Type';

import { useUserState } from './UserModel';
import { useSelectedAssignState } from './AssignmentModel';

/* answer */

const AnswerState = createContext<QuestionAnswerObj[]>([]);
const AnswerDispatch = createContext<Dispatch<QuestionAnswerObj[]>>(() => { });
const SaveAnswer = createContext<(answer : Array<AnswerObj>) => void>(() => { });

export const AnswerContextProvider = ({ children }: childrenObj) => {
	const [answer, setAnswer] = useState<QuestionAnswerObj[]>([]);
	const user = useUserState();
	const selectedAssign = useSelectedAssignState();
	const history = useHistory();

	useEffect(() => {
		// select   answer 
		selectedAssign &&
		fetchAnswer();
	}, [user, selectedAssign]);

	const fetchAnswer = async () => {
		console.log("fetchAnswer");

		if(!(selectedAssign && user)){
			setAnswer([]);
			return;	
		}

		axios.get(`${process.env.REACT_APP_SERVER}/answer/${selectedAssign.assignmentId}`, { withCredentials: true })
			.then(res => {
				console.log(res.data);
				setAnswer(res.data);
			})
			.catch(err => {
				const status = err?.response?.status;
				if (status === undefined) {
					alert("       ." );
				}
				else if (status === 400) {
					alert(`   .  . (${status})`);
				}
				else if (status === 401) {
					alert(`  . (${status})`);
					document.cookie = "access_token=; expires=Thu, 01 Jan 1970 00:00:00 UTC;";
					history.push("/");
				}
				else if (status === 404) {
					alert("   .");
				}
				else if (status === 500) {
					alert("  .    ...");
				}
				setAnswer([]);
			});
	}

	const saveAnswer = (ans : Array<AnswerObj>) => {
		console.log("saveAnswer");

		if(!(selectedAssign && user)){
			console.log("set answer undefined in model");
			setAnswer([]);
			return;
		}
		if(user.type === 'student') {
			axios.post(`${process.env.REACT_APP_SERVER}/answer/${selectedAssign.assignmentId}`, {
				answers : ans
			}, { withCredentials: true })
				.then(res => {
					console.log(res);
					setAnswer([res.data]);
				})
				.catch(err => {
					const status = err?.response?.status;
					if (status === undefined) { 
						alert("       . (Main.js)\n" + JSON.stringify(err));
					}
					else if (status === 400) {
						alert(`   .  . (${status})`);
					}
					else if (status === 401) {
						alert(`  . (${status})`);
						document.cookie = "access_token=; expires=Thu, 01 Jan 1970 00:00:00 UTC;";
						// window.location.href = `${window.location.origin}`;
					}
					else if (status === 404) {
						//       .
						// api  
					}
					else if (status === 500) {
						alert("  .    ...");
					}
					setAnswer([]);
				});
		}
		else { // 
			console.log(ans);
			axios.post(`${process.env.REACT_APP_SERVER}/answer/${selectedAssign.assignmentId}`, {
				answers : ans
			}, { withCredentials: true })
				.then((res) => {
					console.log(res.data);
					setAnswer(res.data);
				})
				.catch(err => {
					const status = err?.response?.status;
					if (status === undefined) { 
						alert("       . (Main.js)\n" + JSON.stringify(err));
					}
					else if (status === 400) {
						alert(`   .  . (${status})`);
					}
					else if (status === 401) {
						alert(`  . (${status})`);
						document.cookie = "access_token=; expires=Thu, 01 Jan 1970 00:00:00 UTC;";
					// window.location.href = `${window.location.origin}`;
					}
					else if (status === 404) {
					//       .
					// api  
					}
					else if (status === 500) {
						alert("  .    ...");
					}
				});
		
		}
	}

	return (
		<AnswerState.Provider value={answer}>
			<AnswerDispatch.Provider value={setAnswer}>
				<SaveAnswer.Provider value={saveAnswer}>
					{children}
				</SaveAnswer.Provider>
			</AnswerDispatch.Provider>
		</AnswerState.Provider>
	);
}

export function useAnswerState() {
	const context = useContext(AnswerState);
	return context;
}

export function useAnswerDispatch() {
	const context = useContext(AnswerDispatch);
	return context;
}

export function saveAnswer() {
	const context = useContext(SaveAnswer);
	return context;
}

